
import { computed, defineComponent } from "vue";

export default defineComponent({
  name: "itemComponent",
  components: {},
  props: {
    item: {
      type: Object,
      default: () => ({}),
    },
  },
  setup(props, { emit }) {
    const showItem = computed(
      () => props.item.element.square && props.item.element.value
    );
    const openModalEditor = () => {
      emit("editItem", props.item);
    };

    const createNewItem = () => {
      console.log("CKE");
      emit("createNewItem");
    };
    return {
      showItem,
      openModalEditor,
      createNewItem,
    };
  },
});
