
import { defineComponent } from "vue";
export default defineComponent({
  name: "modalComponent",
  props: {
    colors: {
      type: Array,
      default: () => [],
    },
  },
  setup(props, { emit }) {
    const pickColor = (color: string) => {
      emit("pickColor", color);
    };
    return {
      pickColor,
    };
  },
});
