
import { computed, defineComponent, reactive } from "vue";
import draggable from "vuedraggable";
import itemComponent from "./item/itemComponent.vue";
import { Item, Element } from "@/models/itemModel";
import modalComponent from "@/components/ui/modalComponent.vue";
import itemEditor from "@/components/boardPage/board/item/itemEditor.vue";
import { useStore } from "vuex";

export default defineComponent({
  name: "boardComponent",
  display: "Simple",
  order: 0,
  components: {
    draggable,
    itemComponent,
    modalComponent,
    itemEditor,
  },
  data() {
    return {
      enabled: true,
      dragging: false,
    };
  },
  setup(props, { emit }) {
    const store = useStore();
    const state = reactive({
      isOpen: false,
      editedItem: {
        element: { color: "", square: false, value: 0 },
        id: 0,
      } as Item,
      emptyItem: {
        element: { color: "", square: false, value: 0 },
        id: 0,
      } as Item,
      controls: "new",
    });

    const openModalEditor = (item: Item) => {
      state.isOpen = true;
      state.editedItem = item;
    };
    const closeModal = () => {
      state.isOpen = false;
    };
    const items = computed({
      get() {
        return store.getters.items;
      },
      set(value: Item[]) {
        store.commit("updateItems", value);
      },
    });

    const editItem = (item: Item) => {
      state.controls = "edit";
      openModalEditor(item);
    };

    const createNewItem = () => {
      state.controls = "new";
      openModalEditor(state.emptyItem);
    };

    return {
      state,
      openModalEditor,
      closeModal,
      editItem,
      createNewItem,
      items,
    };
  },
});
